/* eslint-disable */
<template>
  <div class="photo-library-container">
    <header class="app-header">
      <h1>Photos</h1>
      <i class="ri-account-circle-line profile-icon" @click="showModal"></i>
    </header>

    <main class="photo-grid-container">
      <div v-for="group in sortedPhotoGroups" :key="group.date" class="date-group">
        <h2 class="date-header">{{ group.date }}</h2>
        <div class="photo-grid">
          <div v-for="photo in group.photos" :key="photo.id" class="photo-thumbnail-wrapper">
            <img :src="photo.src" :alt="photo.description" class="photo-thumbnail" @click="showModal"/>
          </div>
        </div>
      </div>
    </main>

    <footer class="bottom-nav">
      <div class="nav-item active">
        <i class="ri-image-line"></i>
        <span>Library</span>
      </div>
      <div class="nav-item" @click="showModal">
        <i class="ri-star-line"></i>
        <span>For You</span>
      </div>
      <div class="nav-item" @click="showModal">
        <i class="ri-layout-grid-line"></i>
        <span>Albums</span>
      </div>
      <div class="nav-item" @click="goToSearch"> <!-- UI transition: PhotoLibrary.vue -> SearchScreen.vue -->
        <i class="ri-search-line"></i>
        <span>Search</span>
      </div>
    </footer>
    <modal ref="errorModal"></modal>
  </div>
</template>

<script>
import { AppData } from '@/AppData';
import { navigateTo } from '@/navStackAndLongTap';

export default {
  name: 'PhotoLibrary',
  data() {
    return {
      photos: [],
    };
  },
  computed: {
    sortedPhotoGroups() {
      if (!this.photos || this.photos.length === 0) return [];

      const groups = this.photos.reduce((acc, photo) => {
        const date = photo.date; // YYYY-MM-DD
        if (!acc[date]) {
          acc[date] = [];
        }
        acc[date].push(photo);
        return acc;
      }, {});

      const sortedDates = Object.keys(groups).sort((a, b) => new Date(b) - new Date(a));

      return sortedDates.map(date => {
        const dateObj = new Date(date);
        const utcDate = new Date(dateObj.getUTCFullYear(), dateObj.getUTCMonth(), dateObj.getUTCDate());
        const formattedDate = utcDate.toLocaleDateString('en-US', {
          year: 'numeric',
          month: 'long',
          day: 'numeric',
        });
        return {
          date: formattedDate,
          photos: groups[date],
        };
      });
    },
  },
  mounted() {
    AppData.initAlbums();
    this.photos = AppData.getAllPhotos();
  },
  methods: {
    goToSearch() {
      navigateTo(this.$router, 'SearchScreen');
    },
    showModal() {
      this.$refs.errorModal.showModal();
    }
  },
};
</script>

<style scoped>
@import url('https://cdn.jsdelivr.net/npm/remixicon@2.5.0/fonts/remixicon.css');

.photo-library-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #121212;
  color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background-color: #1e1e1e;
  border-bottom: 1px solid #333;
}

.app-header h1 {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
}

.profile-icon {
  font-size: 28px;
  cursor: pointer;
}

.photo-grid-container {
  flex-grow: 1;
  overflow-y: auto;
  padding: 0 8px 80px 8px; /* Add padding to bottom to avoid overlap with nav bar */
}

.date-group {
  margin-bottom: 24px;
}

.date-header {
  font-size: 18px;
  font-weight: 600;
  padding: 16px 8px 8px 8px;
  margin: 0;
  color: #e0e0e0;
}

.photo-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 4px;
}

.photo-thumbnail-wrapper {
  width: 100%;
  padding-top: 100%; /* 1:1 Aspect Ratio */
  position: relative;
}

.photo-thumbnail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px;
  cursor: pointer;
}

.bottom-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-around;
  background-color: rgba(20, 20, 20, 0.85);
  backdrop-filter: blur(10px);
  padding: 8px 0;
  border-top: 1px solid #333;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #8e8e93;
  cursor: pointer;
  font-size: 10px;
  flex-grow: 1;
}

.nav-item i {
  font-size: 24px;
  margin-bottom: 2px;
}

.nav-item.active {
  color: #0a84ff;
}
</style>